.page {
  min-width: @xs-width;
  height: 100%;

  margin: 0 auto;

  font-family: @font-page;
  font-size: 15px;
  line-height: 20px;
  vertical-align: middle;

  color: @color-font-black;
  background-color: @color-back-grey;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;

  // min-height: 100%;
  margin: 0;
}
